.search-bar {
    width: 100%;
    height: 35px;
    border: none;
    background-color: rgba(240, 248, 255, 0.691);
    padding: 2px 5px;
    font-weight: bolder;
    font-size: 1.2em;
    display: inline;
    color: rgba(0, 0, 0, 0.5);
    font-family: monospace;
}

.search-bar:focus {
    border: none;
    outline: none;
}

.search-container {
    display: -ms-flexbox;
    display: flex;
    padding: 2px 10px;
    -ms-flex-align: center;
        align-items: center;
    background-color: #f1f1f1;
}


